/* Suggested CSS for improved design */

.container {
  max-width: 500px;
  margin: 40px auto;
  padding: 0 16px;
}

.screen {
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.08);
  padding: 32px 24px;
}

.header h1 {
  font-size: 2rem;
  margin-bottom: 8px;
}

.header p {
  color: #888;
  margin-bottom: 24px;
}

.form-group {
  margin-bottom: 28px;
}

.form-group label {
  font-weight: 500;
  margin-bottom: 8px;
  display: block;
  font-size: 1.1rem;
}

input[type="number"], select {
  width: 100%;
  padding: 10px 12px;
  border-radius: 8px;
  border: 1px solid #ddd;
  font-size: 1rem;
  margin-top: 6px;
  box-sizing: border-box;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin-top: 10px;
}

.grid-item {
  background: #f7f7f7;
  border-radius: 10px;
  padding: 16px 12px;
  text-align: center;
  cursor: pointer;
  min-width: 90px;
  transition: box-shadow 0.2s, border 0.2s;
  border: 2px solid transparent;
  box-shadow: 0 2px 8px rgba(0,0,0,0.03);
}

.grid-item.selected {
  border: 2px solid #007bff;
  box-shadow: 0 4px 16px rgba(0,123,255,0.08);
  background: #eaf4ff;
}

.btn {
  padding: 10px 24px;
  border-radius: 8px;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  transition: background 0.2s;
}

.btn-primary {
  background: #007bff;
  color: #fff;
}

.btn-primary:hover {
  background: #0056b3;
}

.btn-secondary {
  background: #f0f0f0;
  color: #333;
}

.btn-secondary:hover {
  background: #e0e0e0;
}